.awura-section-padding{
  padding: 130px 0;
  @include max(991px) {
    padding: 100px 0;
  }
  @include max(991px) {
    padding: 80px 0;
  }
}

.awura-section-pb{
  padding-bottom: 130px;
  @include max(991px) {
    padding-bottom: 100px;
  }
  @include max(991px) {
    padding-bottom: 80px;
  }
}

.awura-section-pt{
  padding-top: 130px;
  @include max(991px) {
    padding-top: 100px;
  }
  @include max(991px) {
    padding-top: 80px;
  }
}

.awura-mt50{
  margin-top: 50px;
  @include max(991px) {
    margin-top: 35px;
  }
}

.awura-p-lg{
  p{
    font-size: 20px;
    line-height: 30px;
    @include max(575px) {
      font-size: 18px;
    }
   }
}

.mb24{
  display: block;
  margin-bottom: 24px;
}