.aximo-default-btn {
  font-size: $button_font_size;
  line-height: $buton_line_height;
  padding: $button_padding;
  border-radius: $buton_border_radius;
  font-weight: $button_font_weight;
  font-family: $button_font_family;
  width: fit-content;
  position: relative;
  z-index: 1;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: inline-block;
  text-align: center;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
  overflow: hidden;
  color: var(--light-bg2)!important;
  background-color: var(--orange-bg);
  position: relative;
  z-index: 0;
  overflow: hidden;
  position:relative;
  height: 60px;
  line-height: 59px;
  transform-style: preserve-3d;
  &.disabled,
  &:disabled {
    opacity: 1;
  }
  &.d-block {
    width: 100%;
  }
  &.pill {
    border-radius: 50px;
  }
  &.btn-icon {
    padding: 9px 24px;
  }
  &:focus {
    box-shadow: none;
  }
}


// icon button

.blog{
  &-btn{
    line-height: 22px;
    font-weight: 600;
    display: flex;
    align-items: center;
    transition: all 0.4s;
    color: var(--accent-color);
    i{
      font-weight: normal;
      font-size: 22px;
      margin-left: 8px;
      transition: all 0.4s;
    }
    &:hover{
      color: var(--accent-color);
    }
    .icon-show{
      opacity: 0;
      transition: all 0.4s;
    }
    &:hover .icon-show{
      opacity: 1;
      margin-right: 32px;
    }
    .icon-hide{
      transition: all 0.4s;
      transform: translateX(-28px);
    }
    &:hover .icon-hide{
      opacity: 0;
    }
    span{
      position: relative;
      transform: translateX(-28px);
    }
  }
}

.awura-btn-icon{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    vertical-align: middle;
    overflow: hidden;
    position: relative;
    font-weight: $button_font_weight;
    gap: 8px;
    text-transform: capitalize;
    color: var(--white-color);
    background-color: var(--accent-color);
    border-radius: $buton_border_radius;
    padding: $button_padding;
    font-size: $button_font_size;
    text-align: left;
    &:hover{
      color: var(--white-color);
    }
    &.awura-small-btn{
      padding: 11px 25px;
    }
}

.btn-icon{
    position: relative;
    overflow: hidden;
    line-height: 1;
    top: 1px;
}
    .btn-icon{
        transition: transform .38s cubic-bezier(.37,.08,.02,.93),opacity .18s ease-out;
    }
  
    .btn-icon i{
        transition: transform .38s cubic-bezier(.37,.08,.02,.93),opacity .18s ease-out;
        font-size: 22px;
        font-weight: 400;
    }

   .button-icon-hide i{
        position: absolute;
        transform: translateX(-20px);
        top: 0;
   }
   .button-icon-show i{
    display: inline-block;
        transform: translateX(0);
   }
   .awura-btn-icon:hover .button-icon-hide i{
        transform: translateX(0);
   }
   .awura-btn-icon:hover .button-icon-show i{
        transform: translateX(20px);
    }