// fonts variable
$primary-font: "Outfit", sans-serif!default;

// color variable
:root {
  --primary-color: #000000;
  --accent-color: #1248FC;
  --white-color: #fff;
  --gray-color: rgba(0, 0, 0, 0.05);
}

$colors: (
 
);

$bodyfontScale: (
  xl: (
    20: 20px,
    lineheight: 32px,
  ),
  lg: (
    18: 18px,
    lineheight: 28px,
  ),
  md: (
    16: 16px,
    lineheight: 24px,
  ),
  xs: (
    14: 14px,
    lineheight: 22px,
  ),
  xss: (
    12: 12px,
    lineheight: 20px,
  ),
);

// button font size
$button_font_size: 16px;
$buton_line_height: 24px;
$buton_border_radius: 50px;
$button_padding: 16px 26px;
$button_font_weight: 600;
$button_font_family: $primary-font;


.awura-dark-bg{
  background-color: var(--primary-color);
}
.awura-gray-bg{
  background-color: var(--gray-color);
}

.light-color{
  color: var(--white-color);
}
