input[type="text"],
input[type="email"],
input[type="password"],
input[type="url"],
input[type="tel"],
input[type="time"],
input[type="date"],
input[type="search"],
input, textarea {
  margin: 0;
  width: 100%;
  height: 60px;
  color:var(--primary-color);
  font-size: 16px;
  border-radius: 50px;
  background-color: transparent;
  padding: 15px 24px !important;
  border: none!important;
  transition: all 0.4s;
  border: 1px solid var(--primary-color)!important;

   &::-webkit-input-placeholder, 
   &::-webkit-input-placeholder{ /* WebKit, Blink, Edge */
      color:    var(--primary-color);
      opacity: 1;
   }
   &:-moz-placeholder, 
   &:-moz-placeholder{ /* Mozilla Firefox 4 to 18 */
      color:    var(--primary-color);
      opacity:  1;
   }
   &::-moz-placeholder, 
   &::-moz-placeholder{ /* Mozilla Firefox 19+ */
      color:    var(--primary-color);
      opacity:  1;
   }
   &:-ms-input-placeholder, 
   &:-ms-input-placeholder{ /* Internet Explorer 10-11 */
      color:    var(--primary-color);
      opacity: 1;
   }
   &::-ms-input-placeholder, 
   &::-ms-input-placeholder{ /* Microsoft Edge */
      color:    var(--primary-color);
      opacity: 1;
   }

   input::placeholder, 
   textarea::placeholder{ /* Most modern browsers support this now. */
      color:    var(--primary-color);
      opacity: 1;
   }
}
input:focus, 
textarea:focus{
  border: 1px solid var(--accent-color)!important;
}
textarea {
  height: 200px;
  resize: none;
  border-radius: 20px;
  @include max(767px) {
    height: 100px;
  }
}


// subscriber footer main
.awura-subscriber{
   position: relative;
   margin-right: 50px;
   input{
      height: 50px;
      padding-right: 50px;
   }
}
button#awura-subscriber-btn{
   top: 5px;
   right: 5px;
   width: 40px;
   height: 40px;
   display: flex;
   font-size: 24px;
   border-radius: 50px;
   align-items: center;
   justify-content: center;
   color: var(--white-color);
   position: absolute;
   transition: all 0.4s;
   background-color: var(--primary-color);
   &:hover{
      background-color: var(--accent-color);
   }
}