

.blog{
  &-single{
    overflow: hidden;
    margin-bottom: 50px;
    transition: all 0.4s;
    &:hover .blog-img img{
      transform: scale(1.1);
    }
  }
  &-img{
    border-radius: 20px;
    overflow: hidden;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: all 0.4s;
    }
  }
  &-content{
    padding-top: 24px;
  }
  &-meta{
    margin-bottom: 15px;
    display: flex;
    flex-wrap: wrap;
    column-gap: 24px;
    row-gap: 15px;
    a{
      color: var(--primary-color);
      font-size: 16px;
      display: inline-flex;
      font-weight: 600;
      align-items: center;
      transition: all 0.4s;
      &:hover{
        color: var(--accent-color);
      }
      i{
        font-size: 22px;
        margin-right: 15px;
        font-weight: 400;
      }
      
    }
  }
  &-title{
    font-size: 32px;
    line-height: 1.25em; /* 116.667% */
    margin-bottom: 15px;
    transition: all 0.4s;
    @include max(767px) { 
      font-size: 28px;
    }
    a{
      color: var(--primary-color);
      &:hover{
        color: var(--accent-color);
      }
    }
    
  }
  &-titles{
    margin-bottom: 24px;
    font-size: 20px;
    line-height: 26px;
    a{
      color: var(--primary-color);
    }
    
  }
}






.blog-single .blog-text {
  margin-bottom: 28px;
  color: var(--primary-color);
  line-height: 30px;
}

// blog sidebar
.sidebar-area{
  @include max(991px) {
    margin-top: 40px;
  }
}
.widget{
  padding: 30px;
  margin-bottom: 24px;
  border-radius: 18px;
  background-color: #F5F6F7;
}
.widget_title{
  font-weight: 600;
  font-size: 20px;
  line-height: 30px;
  letter-spacing: -.02em;
  margin-bottom: 20px;
}
.widget select, 
.widget input {
  padding: 15px !important;
  padding-left: 50px !important;
  height: 60px;
  width: 100%;
  transition: all 0.4s;
  border-radius: 6px;
  border: 1px solid #e7e7e8 !important;
  background-color: var(--white-color);
  &:focus{
    border: 1px solid var(--accent-color)!important;
  }
 
}
.widget .search-form{
  position: relative;
}
.widget .search-form button {
  position: absolute;
  left: 20px;
  top: 14px;
  i{
    font-size: 24px;
    color: var(--accent-color);
  }
}

.widget_nav_menu li, 
.widget_pages li, 
.widget_archive li, 
.widget_categories li {
  display: block;
  position: relative;
  padding: 8px 0;
  &:first-child{
    padding-top: 0;
  }
  &:last-child{
    padding-bottom: 0;
    border-bottom: none;
  }
}

.widget_nav_menu a, 
.widget_pages a, 
.widget_archive a, 
.widget_categories a,
.widget_categories span {
  transition: all .4s;
  color: var(--primary-color);
 
}
.widget_categories span{
  float: right;
}
.widget_nav_menu a:hover, 
.widget_pages a:hover, 
.widget_archive a:hover, 
.widget_categories a:hover {
  color: var(--accent-color);
}


.recent-post {
  display: flex;
  margin-bottom: 20px;
  gap: 15px;
  &:hover .media-img img{
    transform: scale(1.1);
  }
  &:last-child{
    margin-bottom: 0;
  }
  .media-img {
    width: 100px;
    height: 90px;
    overflow: hidden;
    border-radius: 10px;
    flex-shrink: 0;
    img{
      width: 100%;
      height: 100%;
      transition: all 0.3s;
    }
  }
  .recent-post-meta{
    margin-bottom: 15px;
    a{
      font-size: 14px;
      color: var(--primary-color);
    }
  }
  .post-title {
    font-weight: 600;
    margin-bottom: 5px;
    line-height: 28px;
    font-size: 18px;
    color: var(--primary-color);
    display: block;
    display: -webkit-box;
    max-width: 100%;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    letter-spacing: -.01em;
    a{
      color: var(--primary-color);
    }
  }
}



.widget_tag_cloud a, 
.widget .tagcloud a,
.tagcloud a {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
  margin-right: 6px;
  padding: 9px 12px;
  border-radius: 6px;
  transition: all .4s;
  color: var(--primary-color);
  background-color: var(--white-color);
  &:hover{
    color: var(--white-color);
    background-color: var(--accent-color);
  }
}

// end blog

.blog-details{
  .blog-img{
    margin-bottom: 30px;
  }
  .blog-content{
    padding: 0;
    h6{
      margin-bottom: 10px;
    }
  }
  
}
.blog-steps-thumb{
  margin: 24px 0 40px;
}
.blog-steps{
  margin-bottom: 24px;
  ul{
    li{
      margin-bottom: 15px;
      position: relative;
      padding-left: 35px;
      &:last-child{
        margin-bottom: 15px;
      }
      img{
        left: 0;
        top: 6px;
        position: absolute;
      }
      span{
        font-weight: 700;
        font-family: $primary-font;
      }
    }
  }
    
  
}

blockquote {
  position: relative;
    font-size: 20px;
    line-height: 30px;
    padding: 30px;
    margin: 0 0 24px;
    display: flex;
    border-radius: 10px;
    align-items: flex-start;
    color: var(--primary-color);
    background: linear-gradient(90deg, rgba(1, 25, 255, .1) 0, rgba(1, 25, 255, 0) 100%);
    gap: 20px;
    font-family: $primary-font;

}

.post-navigation {
  display: flex;
    padding: 0 0 24px;
    flex-wrap: wrap;
    gap: 24px;
    justify-content: space-between;
    border-bottom: 1px solid #E3E3EC;
}


.nav-previous, 
.nav-next {
  display: flex;
    line-height: 1;
    align-items: center;
    font-weight: 600;
    transition: all .4s;
    color: var(--accent-color);
    i{
      font-size: 22px;
      font-weight: 500;
    }
    
}

.nav-previous i{
  margin-right: 5px;
}
.nav-next i{
  margin-left: 5px;
}
.nav-next{
  text-align: right;
  img{
    margin-left: 10px;
  }
  span{
    justify-content: flex-end;
  }
}


.share-links {
  margin: 30px 0;
  .tagcloud a, 
  .widget .tagcloud a, 
  .widget_tag_cloud a{
    margin-bottom: 0;
    background-color: #F5F6F7;
    &:hover{
      background-color: #2B4DFF;
    }
  }
}




.blog-inner-title {
  margin-bottom: 30px;
  font-size: 24px;
  margin-top: 50px;
}
.comments-wrap {
  padding-top: 60px;
  .post-comment {
    display: flex;
    gap: 24px;
    position: relative;
    padding-bottom: 24px;
    margin-bottom: 24px;
    border-bottom: 1px solid var(--off-white-bg);
  }
  .comment-avater{
    width: 90px;
    height: 90px;
    border-radius:10px;
    flex-shrink: 0;
    overflow: hidden;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  .name{
    margin-bottom: 7px;
    font-size: 18px;
  }
  span.commented-on{
    font-size: 16px;
    margin-bottom: 15px;
    display: inline-block;
    color: #585B6F;
    margin: 0;
  }

  a.replay-btn{
    font-weight: 600;
    transition: all .4s;
    color: var(--accent-color);
    display: flex;
    align-items: center;
    margin-top: 10px;
    i{
      margin-right: 6px;
    font-size: 20px;
    }
  }
  .children{
    padding-left: 90px;
    @include max(767px) {
      padding-left: 30px;
    }
    
  }
}


.comment-respond{
  .form-group{
    margin-bottom: 24px;
    input, 
    textarea{
      margin: 0;
      width: 100%;
      height: 54px;
      font-weight: 400;
      color: var(--primary-color);
      border-radius: 0;
      background-color: transparent;
      padding: 15px 0 !important;
      border: none !important;
      transition: all .4s;
      border-bottom: 1px solid #E3E3EC !important
    }
    textarea{
      height: 140px;
      @include max(767px) {
        height: 120px;
      }
    }
    input:focus, 
    textarea:focus{
      border-bottom: 1px solid var(--accent-color)!important;
    }
  }
  p{
    margin-top: 15px;
  }
}


input::-webkit-input-placeholder, 
textarea::-webkit-input-placeholder{ /* WebKit, Blink, Edge */
    color:    #8A8C9A;
    opacity: 1;
}
input:-moz-placeholder, 
textarea:-moz-placeholder{ /* Mozilla Firefox 4 to 18 */
   color:    #8A8C9A;
   opacity:  1;
}
input::-moz-placeholder, 
textarea::-moz-placeholder{ /* Mozilla Firefox 19+ */
   color:    #8A8C9A;
   opacity:  1;
}
input:-ms-input-placeholder, 
textarea:-ms-input-placeholder{ /* Internet Explorer 10-11 */
   color:    #8A8C9A;
   opacity: 1;
}
input::-ms-input-placeholder, 
textarea::-ms-input-placeholder{ /* Microsoft Edge */
   color:    #8A8C9A;
   opacity: 1;
}

input::placeholder, 
textarea::placeholder{ /* Most modern browsers support this now. */
   color:    #8A8C9A;
   opacity: 1;
}
.awura-check input {
  padding: 0;
  height: initial;
  width: initial;
  margin-bottom: 0;
  display: none;
  cursor: pointer;
}
.awura-check label {
  position: relative;
  cursor: pointer;
  margin-bottom: 40px;
  color: #585B6F;
}
.awura-check label:before {
  content: "";
  -webkit-appearance: none;
  background-color: transparent;
  border: 2px solid #0a165e;
  padding: 6px;
  display: inline-block;
  position: relative;
  vertical-align: middle;
  cursor: pointer;
  margin-right: 12px;
  margin-top: -5px;
}
.awura-check input:checked+label:after {
  content: "";
  display: block;
  position: absolute;
  top: 7px;
  left: 6px;
  width: 5px;
  height: 8px;
  border: solid var(--accent-color);
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}
button#comment-btn {
  font-size: 16px;
  line-height: 1;
  padding: 22px 27.3px;
  border-radius: 6px;
  font-weight: 600;
  font-family: Sora, sans-serif;
  width: fit-content;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: inline-flex;
  align-items: center;
  text-align: center;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
  color: var(--white-color) !important;
  background-color: var(--accent-color);
  z-index: 0;
  overflow: hidden;
  position: relative;
  letter-spacing: -.01em;
  transform-style: preserve-3d;
}
// end blog and blog details
.blog-details{
  .blog-meta{
    padding-bottom: 15px;
    margin-bottom: 24px;
    border-bottom: 1px solid #e7e7e8;
  }
  .blog-meta a {
    color: #0A165E;
    position: relative;
    padding-left: 28px;
    &:before {
      content: none;
    }
    i{
      position: absolute;
      left: 0;
      top: 2px;
      line-height: 1;
      color: var(--accent-color);
    }
    
  }
}



// end
